import Totop from '../../common/totop.vue'
export const mixinsbacktop={
    data:function(){
        return{
            totopisshow:false,
        }
    },
    components:{
         totop:Totop,   
    },
    methods:{
        backtop:function(){
        this.$refs.scroll.backTo(0,0);
        },
    },
    
}

