<template>
<div class="detail"> 
<!-- 顶部导航栏 -->
<top-bar @clicked="topbarclicked" 
:currentindex="topbarindex" />

<scroll ref="scroll" @scrolling="getposition"  
:probetype="3" >
<!-- 轮播图 -->
<lun-bo :imglist="data.lunboimgs" 
@imghasload="lunboimgload" />

<!-- 商品相关信息块 -->
<info  :info="data.goodsinfo"  />

<!-- 参数 -->
<div ref="cs" class="contentimg">
<p class="theme">参数</p>
<img src="./img/cs.png" alt=""  @load="csimgload"  />
</div>

<!-- 评论 -->
<div ref="pl" class="contentimg" >
<p class="theme">评论</p>
<img src="./img/pl.png" alt=""  @load="plimgload"   />
</div>


<!-- 推荐 -->
<div ref="tj" class="contentimg">
<p class="theme">推荐</p>
<img src="./img/tj.png" alt=""  @load="tjimgload"   />
</div>


</scroll>
<!-- 组件返回顶部 -->
<totop  @click.native="backtop" v-show="totopisshow"/>

<!-- 底部栏 -->
<bottombar  @clicked="addtocart" />

<!-- 组件吐司 -->
<toast ref="toast" />
</div>
</template>

<script>
//import {data1,data2,data3} from "./data.js"
import scroll from "../../common/scroll.vue"
import topbar from "./topbar.vue"
import lunbo from "../../common/lunbo.vue"
import info from "./info.vue"
import {example} from "../../common/mixins/example.js"
import {mixinsbacktop} from "../../common/mixins/backtop.js"
import Bottombar from "./bottombar.vue"
import toast from "../../common/toast/toast.vue"
import net from "../../../axios/detailnet.js"

export default {
    name:"detail",
    data:function(){
        return{
         
          id:"",
          data:{goodsinfo:{}},
          topbarindex:0,
          baritemtopvalue:[0,],
         
        }
    },
    components:{
           topBar:topbar,
           lunBo:lunbo,
           info:info,
           scroll:scroll,
           bottombar:Bottombar,
           toast:toast
        },
    
    methods:{
        net:net,
        imghasload:function(){
        this.$refs.scroll.refresh();
        this.baritemtopvalue=[0,];
        this.baritemtopvalue.push(this.$refs.cs.offsetTop);
        this.baritemtopvalue.push(this.$refs.pl.offsetTop);
        this.baritemtopvalue.push(this.$refs.tj.offsetTop);
        },
       lunboimgload:function(){
         this.imghasload();
        //console.log(this.baritemtopvalue)
        },
        csimgload:function(){
             this.imghasload();
        },
        plimgload:function(){
             this.imghasload();
        },
        tjimgload:function(){
        this.imghasload();
        },
topbarclicked(index){
this.topbarindex=index;
this.$refs.scroll.backTo(0,-this.baritemtopvalue[index],200);
},
getposition:function(position){
  let y=-position.y;
  if(y<this.baritemtopvalue[1])
  this.topbarindex=0;
  else if(y<this.baritemtopvalue[2])
  this.topbarindex=1;
  else if(y<this.baritemtopvalue[3])
  this.topbarindex=2;
  else 
  this.topbarindex=3;

    
     if(y>360)
     this.totopisshow=true;
     else
     this.totopisshow=false;
     },
     addtocart:function(){
        let neworder={};
        neworder.id=this.data.goodsinfo.id;
        neworder.img=this.data.goodsinfo.cartimg;
        neworder.title=this.data.goodsinfo.desc;
        neworder.specificchoice=this.data.goodsinfo.haschoosecs;
        neworder.price=this.data.goodsinfo.newprice;
        //neworder.count=0;
        
        //操作vuex的state数据步骤，
        //1、commit->mutation->state
        //2、dispatch->action->mutation->state
        //异步操作和多种逻辑需要经过action
   
        // this.$store.commit("showtocart",neworder)
        //dispatch方法会返回一个promise，经过打印可以了解

        //我们这里使用action传过来的promise，
        //确保他们成功再继续操作
        this.$store.dispatch("showtocart",neworder)
        .then((rel)=>{
        this.$toast.toast(rel)
        // 插件使用方式
        // this.$refs.toast.toast(rel)
        //组件使用方式
        },
        ()=>{ this.$toast.toast("抱歉,添加失败")});
        
        

     }
    },
    created:function(){
      
     
       this.id=this.$route.params.id;
         this.net();
        //通过params拿到参数对象,
        //拿到活跃路由里的get参数对象里的id属性值
       //console.log(this.id);
      //由于keep-alive的原因,该组件不会被销毁，就不会创建
     //新的，因此每次得到的id都是一样的，我们需要在
     //keep-alive里加exclude="detail" ,detail是组件的name属性值
       
       //通过id不同获取不同数据
        // if(this.id==0)
        // this.data=data1;
        // else if(this.id==1)
        
        // this.data=data2;
        // else
        // this.data=data3;

        this.$nextTick(()=>{
             //当dom元素全部生成完毕再调用
             //这个并不能解决问题，因为图片
             //是在之后加载的,要解决scroll的计算错误
             //以及offsettop的获取值错误，要在imgload
             //里调用
        });
    },
    //vue的混入
     mixins:[example,mixinsbacktop],
    mounted:function(){
       
    },
   updata:function(){
       //当数据加载,更新就调用这个函数
   },
   
}
</script>

<style scoped>
.wrapper{
    z-index: 9999;
    background-color: white;
    bottom: 62px;
    }
 .theme{
     margin:15px 0;
     text-align: center;
     color: rgb(248, 150, 166);
 }
 .contentimg img{
     width: 100%;
 }
</style>