import {req}  from  "./req"
export default function(){
        var that=this;   //this的指向可能会改变，以防万一
        req({    //instance方法默认返回一个promise
     url:"/detail/data",    //请求路径
    
      }).then((rel)=>{
           //对响应结果进行打印  
           
          //  console.log(rel);
           if(that.id==0)
           that.data=rel.data1
           //that.data=data1;
           else if(that.id==1)
           that.data=rel.data2
           //that.data=data2;
           else
           that.data=rel.data3
         
      },(rej)=>{
       //响应失败  
       console.log(rej)
     })
    }
