<template>
<div class="detail"> 
<top-navbar class="topbar">
<div slot="left" class="left" @click="back">
<span>&lt;</span>
</div>
<div slot="center" class="center">
<span v-for="(value,index) in detail" :key=index
@click="itemclicked(index)" :class="{bepink:currentindex==index}">
{{value}}
</span>
</div>
</top-navbar>
</div> 
</template>

<script>
import topnavbar from "../../common/topnavbar.vue" 
export default {
    data:function(){
        return{
           detail:["商品","参数","评论","推荐"],
           
        }
    },
    props:{
       currentindex:{
           type:Number,
           default:0
       }
    },
    components:{
            topNavbar:topnavbar
        },
    methods:{
        itemclicked:function(index){
          this.$emit("clicked",index)
        },
        back:function(){
           this.$router.go(-1);
        },
    },
    
}
</script>

<style scoped>
.bepink{
    color: rgb(252, 151, 168);
}
.topbar{
    background-color: white;
}
.left span{
    font-size: 28px;
    cursor: pointer;
}
.center{
    display: flex;
    justify-content: space-between;
}
.center span{
    font-size: 14px;
    cursor: pointer;
}
</style>